body {
  margin: 0 !important;
  padding: 0 !important;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100%;
}
.App {
    margin: 0px auto;
}

.main {
    margin-left: 250px;
}

li svg.box-plot {
    height: 100%;
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    /* z-index: -1; */
}

.tooltip {
    width: 100%;
    pointer-events: none;
}

::-webkit-scrollbar { 
    display: none; 
}

html, body {
    height: 100%;
    margin: 0;
}

h4 {
    margin: 0;
    padding: 0;
}
/* first child will be clicked */
#radar-wrapper path:nth-child(1) {
    fill: #e57373 !important;
    stroke: #e57373 !important;   
}

/* second child will be hovered */
#radar-wrapper path:nth-child(2) {
    fill: #64b5f6 !important;
    stroke: #64b5f6 !important;
}
.barchart-rect:hover {
    fill: #5a6d94 !important;
}

.barchart-rect {
    fill: #d6d6d6;
    opacity: .8;
}

.barchart-rect.bar-click {
  fill: #b71c1c !important;
  opacity: .4;
}

.barchart-rect.bar-hover {
  fill: #2979ff !important;
  opacity: .4;
}

.d3-tip {
    line-height: 1;
    font-weight: bold;
    padding: 10px;
    background: rgba(29, 39, 55, 0.8);
    color: #fff;
    border-radius: 7px;
    z-index: 2000;
  }

/* Creates a small triangle extender for the tooltip */
.d3-tip:after {
    box-sizing: border-box;
    display: inline;
    font-size: 10px;
    width: 100%;
    line-height: 1;
    color: rgba(29, 39, 55, 0.8);
    content: "\25BC";
    position: absolute;
    text-align: center;
  }
  
  /* Style northward tooltips differently */
  .d3-tip.n:after {
    margin: -1px 0 0 0;
    top: 100%;
    left: 0;
  }
.suggested-card {
    width: 315px;
    margin-left: 10px;
    padding-top: 10px;
    margin-bottom: 10px;
    overflow: scroll;
}

.suggested-card:hover {
    background: #f7f7f7;
}

.similar-card {
    width: 350px;
    margin-left: 10px;
    padding-top: 10px;
    margin-bottom: 10px;
    overflow: scroll;
}

.similar-card:hover {
    background: #f7f7f7;
}
/* Styles for StripPlot */

.y.axis line {
    fill: none;
    stroke: #d6d6d6;
    shape-rendering: crispEdges;
    stroke-width: 1px;
}

.x.axis line {
    display: none;
}

.tick.g-baseline line {
    stroke: #d6d6d6;
    stroke-dasharray: 0;
    stroke-width: 2px;
}

.axis text {
    font-size: 12px;
    pointer-events: none;
    fill: #a6a6a6;
}

text.axis-label {
    text-anchor: start !important;
    font-size: 16px;
    fill: #a6a6a6;
}

.domain {
    /* display: none; */
    color: #e6e6e6;
}

.percentline {
    stroke: #1D2737;
    stroke-width: 5;
    opacity: .6;
}

.percentline.linehover {
    stroke: #2979ff;
}

.percentline.lineclick {
    stroke: #b71c1c;
    stroke-width: 7;
}
